import React, { useState } from "react";
import "./Header.css";
import { Fade } from "react-reveal";
import { NavLink } from "react-router-dom";
import { greeting, settings } from "../../portfolio.js";
import { CgSun } from "react-icons/cg";
import { HiMoon } from "react-icons/hi";
import { style } from "glamor";

function Header(props) {
  const theme = props.theme;

  const styles = style({
    cursor: "pointer",
    height: "45px",
    width: "45px",
    marginRight: "5px",
    marginLeft: "15px",
    paddingTop: "5px",
    borderRadius: "50%",
    border: "none",
    alignItems: "center",
    justifyContent: "center",
    backgroundColor: theme.name === "light" ? "#7CD1F7" : "#292C3F",
    outline: "none",
    transition: "all 0.2s ease-in-out",
    ":hover": {
      boxShadow: `0 3px 8px ${theme.name === "light" ? "#F7D774" : "#646464"}`,
    },
  });

  const link = settings.isSplash ? "/splash" : "/home";
  const [currTheme, setCurrTheme] = useState(theme.name);

  function changeTheme() {
    if (currTheme === "light") {
      props.setTheme("dark");
      localStorage.setItem("theme", "dark");
      setCurrTheme("dark");
    } else {
      props.setTheme("light");
      localStorage.setItem("theme", "light");
      setCurrTheme("light");
    }
  }

  const icon =
    theme.name === "dark" ? (
      <HiMoon
        strokeWidth={1}
        size={20}
        color={theme.name === "light" ? "#F9D784" : "#A7A7A7"}
      />
    ) : (
      <CgSun
        strokeWidth={1}
        size={20}
        color={theme.name === "light" ? "#F9D784" : "#A7A7A7"}
      />
    );

  const activeLinkStyle = ({ isActive }) => ({
    fontWeight: isActive ? "bold" : "normal",
    borderRadius: 5,
    color: theme.text,
    textDecoration: "none",
  });

  return (
    <Fade top duration={1000} distance="20px">
      <header className="header">
        <NavLink to={link} className="logo">
          <span style={{ color: theme.text }}></span>
          <span className="logo-name" style={{ color: theme.text }}>
            {greeting.logo_name}
          </span>
          <span style={{ color: theme.text }}></span>
        </NavLink>
        <input className="menu-btn" type="checkbox" id="menu-btn" />
        <label className="menu-icon" htmlFor="menu-btn">
          <span className="navicon"></span>
        </label>
        <ul className="menu">
          <li>
            <NavLink to="/home" style={activeLinkStyle}>
              Home
            </NavLink>
          </li>
          <li>
            <NavLink to="/education" style={activeLinkStyle}>
              Education and Certifications
            </NavLink>
          </li>
          <li>
            <NavLink to="/experience" style={activeLinkStyle}>
              Experience
            </NavLink>
          </li>
          <li>
            <NavLink to="/projects" style={activeLinkStyle}>
              Projects
            </NavLink>
          </li>
          <li>
            <NavLink to="/contact" style={activeLinkStyle}>
              Contact and Resume
            </NavLink>
          </li>
          <button {...styles} onClick={changeTheme}>
            {icon}
          </button>
        </ul>
      </header>
    </Fade>
  );
}

export default Header;
